<template>
  <Form ref="formInline" :model="formInline" :rules="ruleInline" >
        <FormItem prop="user">
            <Input type="text" v-model="formInline.user" placeholder="Username">
                <Icon type="ios-person-outline" slot="prepend"></Icon>
            </Input>
        </FormItem>
        <FormItem prop="password">
            <Input type="password" v-model="formInline.password" placeholder="Password">
                <Icon type="ios-lock-outline" slot="prepend"></Icon>
            </Input>
        </FormItem>
        <FormItem>
            <Button type="primary" @click="handleSubmit()">修改</Button>
        </FormItem>
    </Form>
</template>
<script>
import {selectOneUser,updateOneUser} from '@/api/api.js'
import { mapActions } from 'vuex';
export default {
        data(){
            return{
                user:'',
                formInline: {
                    user: '',
                    password: ''
                },
                ruleInline: {
                    user: [
                        { required: true, message: 'Please fill in the user name', trigger: 'blur' },
                        { type: 'string', min: 1, message: 'The password length cannot be less than 1 bits', trigger: 'blur' }
                    ],
                    password: [
                        { required: true, message: 'Please fill in the password.', trigger: 'blur' },
                        { type: 'string', min: 1, message: 'The password length cannot be less than 1 bits', trigger: 'blur' }
                    ]
                }
            }
        },
        methods:{
            ...mapActions(['updateUser']),
            handleSubmit(){

                let data={userId:this.user.userId,userName:this.formInline.user,userPwd:this.formInline.password}
                this.updateUser(data).then(res=>{
                    if(res.data.status==201){
                         this.$Message.success('密码修改失败')
                         return
                    }
                    this.$Message.success('修改成功')
                     this.$router.push({name:'blank'}) 
                   setTimeout(() => {
                        this.$router.push({name:'canteenList'})
                   }, 1); 
                })
            }
        },
        mounted(){
             this.user=JSON.parse(localStorage.getItem('user'))
             let data={userId:this.user.userId}
             selectOneUser(data).then(res=>{
                 let user=res.data.data
                 this.formInline.user=user.userName
                 
             })
        }
}
</script>
